  async getLastFinalizedBlockNumber(){
    const { api } = this.state;
    const {derive, query} = api;
    const {chain} = derive;
    return chain.bestNumberFinalized();
  }

  async getBlockNumberFromHash(hash) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockData = await chain.getBlock(hash);
      return parseInt(blockData.block.header.number.toString());
    }
    return 0;
  }

  async getHashFromNumber(number) {
    const { api } = this.state;
    if (api.type) {
      const { rpc } = api;
      const { chain } = rpc;
      const blockHash = await chain.getBlockHash(number);
      return blockHash;
    }
    return 0;
  }

