  async clickListener(event) {
    this.setState({ loading: true });
    // const { currentTarget, target, nativeEvent, type } = event;
    // const parent = currentTarget.parentElement;
    // const { innerText, parentElement } = target;
    const form = this.inputForm.current;
    // console.log(form);
    const { api, percentLoaded } = this.state;
    let { endBlock, endpoint, startBlock } = this.state;
    startBlock = parseInt(form.getFieldValue('startBlock')) || startBlock;
    endBlock = parseInt(form.getFieldValue('endBlock')) || endBlock;
    endpoint = parseInt(form.getFieldValue('endpoint')) || endpoint;
    const blockDifference = endBlock - startBlock;
    const endBlockData = await this.getBlockData(endBlock);
    const startBlockData = await this.getBlockData(startBlock);
    let events = [];
    const percentTicker = 100 / blockDifference;
    for (let i = 0; i <= blockDifference; i++) {
      // const percentLoaded = Math.round((i * percentTicker + Number.EPSILON) * 100) / 100;
      this.setState({ percentLoaded: Math.round(i * percentTicker) });
      const blockHash = await this.getHashFromNumber(startBlock + i);
      const blockEvents = await this.getEventsFromHash(blockHash);
      const formattedEvents = await this.parseEventData(blockEvents);
      formattedEvents.forEach((item, index) => {
        const { blockNumber } = item;
        item.key = `${i + blockNumber + index}`;
        events.push(item);
      });
    }
    await this.populateTable(events);
  }

